<template>
  <div id="bill" ref="bill">
    <mt-header title="账单" style="height:.88rem;background-color:#4a57ff;position: fixed;
    width: 100%;z-index:20;">
      <!-- <router-link to="/" > -->
        <mt-button icon="back" slot="left" @click="goBack"></mt-button>
      <!-- </router-link> -->
      <img slot="right" :class="showSelect?'active':''" @click="selectShow" style="width:.4rem;height:.44rem" :src="ImgUrl+'home/saixun.png'" alt="">
    </mt-header>
    <div class="mask" v-if="showSelect"></div>
    <div class="selectContent" :class="showSelect?'active':''">
      <div class="selectBody">
        <div class="title">交易日期</div>
        <div class="btnSelect">
          <div v-for="(item,index) in timeList" :key="index" @click="selectTime(item.value,index)" :class="timeActiveindex==index?'active':''">
          {{item.name}}
          </div>
          
        </div>
        <div class="btn_time">
          <div @click="changetime(1)"><span v-if="!startTime1"><img :src="ImgUrl+'home/time.png'">开始时间</span> <span v-else>{{startTime1}}</span></div>
          <div @click="changetime(2)"><span v-if="!endTime1"><img :src="ImgUrl+'home/time.png'">结束时间</span> <span v-else>{{endTime1}}</span></div>
        </div>
        <div class="title er">交易类型</div>
        <div class="btnSelect1">
          <div v-for="(item,index) in transactionState" :key="index" v-if="index<3" @click="selectState(item.state,index)" :class="transactionIndex == index?'active':''">
            {{item.name}}
          </div>
          
        </div>
        <div class="btnSelect1">
          <div v-for="(item,index) in transactionState" :key="index" v-if="index>=3" @click="selectState(item.state,index)" :class="transactionIndex == index?'active':''">
            {{item.name}}
          </div>
          
        </div>
        <div class="bottom_sub">
          <div class="rest" @click="reset">重置</div>
          <div class="submit" @click="submit">确定</div>
        </div>
      </div>
      <calendar
          v-model="calendarShow"
 
          @change="handelChange">
      </calendar>
      <!-- <mt-datetime-picker
        ref="picker"
        v-model="pickerValue"
        type="date"
        year-format="{value} 年"
        month-format="{value} 月"
        date-format="{value} 日"
        @confirm="handleConfirm">
      </mt-datetime-picker> -->
    </div>
    <div class="content" >
      <div class='loading' v-if="busy1"><img :src="ImgUrl+'home/loading.gif'" alt=""></div>
      <div class="item_content" v-for="(item,index) in list" :key="index">
        <div class="top">
          <div>
            <span v-if="item.type==7||item.type==8">提现</span>
            <span v-if="item.type==2">被分享人消费奖励</span>
            <span v-if="item.type==4">被分享人消费奖励退款</span>
          </div>
          <span class="addPrice" v-if="item.inout_type==1">+{{item.amount}}</span>
          <span class="delPrica" v-if="item.inout_type==2">-{{item.amount}}</span>
        </div>
        <div class="bottom">
          <span>{{item.updated_at}}</span>
          <span v-if="item.status == 1||item.status == 2">提现中</span>
          <span v-if="item.status == 3">提现成功</span>
          <span v-if="item.type==7&&(item.status == 4||item.status == 5)">提现失败</span>
        </div>
      </div>
      <div class='loading' v-if="loading"><img :src="ImgUrl+'home/loading.gif'" alt=""></div>
      <div class="di" v-if="page*10 > total&&list.length!=0">一一 我是有底线的 一一</div>
      <!-- <div class="item_content">
        <div class="top">
          <span>被分享人消费奖励退款</span>
          <span class="delPrica">-500</span>
        </div>
        <div class="bottom">
          <span>2018-10-11 16:04:00</span>
        </div>
      </div> -->
    </div>
    <div class="nodata" v-if="list.length==0&&show">
      <div>
        <div><img :src="ImgUrl+'home/nodata.png'"></div>
        <div>暂无账户交易数据</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Todate } from '@/utils/index.js'
import { goBack,getTokus } from '@/utils/native.js'
import { getBillList } from '@/api/bill.js'
export default {
  data() {
    return {
      timeNum: 1, // 时间选择框是哪个 1是开始时间 2是结束时间
      startTime:'', // 开始时间
      endTime:'', // 结束时间
      startTime1:'',
      endTime1:'',
      show:false,
      busy1:true,
      loading:false,
      showSelect:false, // 显示选择局域栏
      timeActiveindex: 0, // 当前选中时间状态框索引
      transactionIndex: 0, // 当前选中交易状态框索引
      type:'',
      pickerValue:'', // 时间插件绑定值
      list:[], // 列表数据
      total:2,
      page:1,
      pageSize:10,
      calendarShow:false,
      flag:false,
      timeList: [{  // 时间状态列表
        name: '全部',
        vlaue: ''
      },{
        name: '今天',
        vlaue: '1'
      },{
        name: '最近7天',
        vlaue: '2'
      }],
      transactionState:[{  // 交易状态列表
        name: '全部',
        state: ''
      },{
        name: '提现',
        state: 1
      },{
        name: '消费奖励',
        state: 2
      },{
        name: '消费奖励退款',
        state: 3
      }]
    };
  },
  components: {
  },
  mounted() {
    
  },
  methods: {
    // 重置
    reset() {
      this.timeActiveindex = 0
      this.transactionIndex = -1
      this.startTime1 =''
      this.endTime1 = ''
      this.startTime = ''
      this.endTime = ''
    },
    scrollFunc(e) { // 计算滑动是否到底函数
      // e = e || window.event;

      
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      let windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
      if (scrollTop + windowHeight >= scrollHeight) {
        if(!this.loading&&this.page*10 < this.total){
          this.loading = true
          this.page++
          this.getBillList() 
        }
      }
      if(!this.busy1&&scrollTop<=0){
        this.page=1;
        this.busy1 = true
        this.getBillList();
      }
    },
    submit() {
      this.page = 1
      this.showSelect = false
      document.documentElement.scrollTop = 0
      document.body.scrollTop = 0
      window.pageYOffset = 0
      this.getBillList() 
    },
    // 获取列表
    getBillList() {
      getBillList({
        startDate:this.startTime1?this.startTime1:this.startTime,
        endDate:this.endTime1?this.endTime1:this.endTime,
        type:this.type,
        page:this.page,
        pageSize:this.pageSize
      }).then(data=>{
        
        this.total=data.data.total
          if(this.page == 1){
            this.list = data.data.list
            this.show = true
          }else {
            this.list = this.list.concat(data.data.list) 
            
          }
        setTimeout(()=>{
          this.flag = false
          this.loading = false
          this.busy1 = false
        },500)
        
      })
    },
    handelChange(val){
      if(this.timeNum == 1) this.startTime1 = val ? Todate(val) : ''
      else this.endTime1 = val ? Todate(val) : ''
      // this.pickerValue = ''
    },
    // 选择开始时间结束时间函数
    changetime (num) { 
      this.timeNum = num
      this.calendarShow = true
      this.timeActiveindex = 0
      // this.$refs.picker.open();
    },
    // 选择时间状态函数
    selectTime(val, index) {
      this.startTime1 = ''
        this.endTime1 = ''
      this.timeActiveindex = index
      switch (index){
        case 0:
        this.startTime = ''
        this.endTime = ''
        break;
        case 1:
        this.startTime = Todate()
        this.endTime = Todate()
        break;
        case 2:
        this.endTime = Todate()
        let date = Date.parse(new Date())
        let data1 = date - 518400000
        this.startTime = Todate(data1)
        break;
        default:
        this.startTime = ''
        this.endTime = ''
        break;
      }
    },
    // 选择交易状态函数
    selectState(val, index){
      this.transactionIndex = index
      this.type = val
    },
    // 状态框显示函数
    selectShow() {
      this.showSelect = !this.showSelect
    },
    // 时间插件选择时间函数
    handleConfirm(val) {
      if(this.timeNum == 1) this.startTime1 = this.pickerValue ? Todate(this.pickerValue) : ''
      else this.endTime1 = this.pickerValue ? Todate(this.pickerValue) : ''
      this.pickerValue = ''
    },
    goBack() {
      goBack()
    }
  },
  created() {
    getTokus(this)
    setTimeout(() =>{
      this.getBillList()
    },500)
    window.addEventListener("scroll", this.scrollFunc); // body滚动
	},
  activated() {},
  watch: {
    $route() {
      //数字滚动特效
    },
    active: {
      handler(val) {},
      deep: true
    }
  }
};
</script>


<style lang="less" scoped>
#bill {
  width: 100%;
  .di{
    text-align: center;
    height: .8rem;
    line-height: .8rem;
  }
  .nodata{
    width:100%;
   
    height:12.46rem;
    position:relative;
    display:flex;
    text-align:center;
    align-items:center;
    flex-wrap:wrap;
    div:nth-of-type(2){
      width:100%;
      margin-top:.8rem;
      font-family: MicrosoftYaHei;
      font-size: .3rem;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      color: #cccccc;
    }
    div:nth-of-type(1){
      text-align:center;
      width:100%;
      img{
        width: 2.6rem;
	      height: 2.68rem;
        display:block;
        margin:0 auto;
      }
    }
  }
  .mask{
    width:100%;
    height:100%;
    position: fixed;
    top:.88rem;
    z-index:2;
    background-color:rgba(0,0,0,.5);
  }
  img{
    transform:rotate(0deg);
    transition:all .5s;
  }
  img.active{
    transform:rotate(180deg);
    transition:all .5s;
  }
  .selectContent.active{
      transform:translateX(0);
      transition:all .5s;
    }
  .selectContent{
    width:100%;
    // height:100%;
    height:calc(100% - .88rem);
    position: fixed;
    // top:.88rem;
    z-index:3;
    // background-color:rgba(0,0,0,.5);
    padding-left:1.04rem;
    transform:translateX(100%);
      transition:all .5s;
    
    .selectBody{
      width:100%;
      height:100%;
      background-color:#fff;
      position: relative;
      
      padding:.6rem .4rem 0;
      .btn_time::before{
        content:'';
        width: .28rem;
        height: .02rem;
        background-color: #bbbbbb;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
      }
      .bottom_sub{
        width:100%;
        height:1rem;
        position:absolute;
        bottom:0;
        left:0;
        display:flex;
        div{
          flex:1;
          text-align:center;
          line-height:1rem;
          font-family: MicrosoftYaHei;
          font-size: .32rem;
          font-weight: normal;
          font-stretch: normal;
          letter-spacing: 0px;
          color: #999999;
          border:.02rem solid #e5e5e5;
        }
        .submit{
          background-color: #4a57ff;
          border:.02rem solid #4a57ff;
          color:#fff;
        }
      }
      .btn_time{
        width:100%;
        height:.72rem;
        display:flex;
        justify-content: space-between;
        position:relative;
        div{
          display:flex;
          align-items: center;
          span{
            display:flex;
            align-items: center;
          }
          img{
            width:.3rem;
            height:.3rem;
            margin-right:.1rem;
          }
        }
      }
      
      .btnSelect,.btn_time,.btnSelect1{
        width:100%;
        height:.72rem;
        display:flex;
        justify-content: space-between;
        margin-top:.4rem;
        div{
          padding:0 .4rem;
          border: solid .02rem #e5e5e5;
          font-size: .24rem;
          color: #999999;
          line-height:.72rem;
          border-radius: .1rem;
          margin-left:.34rem;
        }
        div:nth-of-type(1){
          margin-left:0;
        }
        div.active{
          color: #ffffff;
          background-color:#4a57ff;
          border: solid .02rem #4a57ff;
        }
      }
      .title{
        height: .32rem;
        line-height:.32rem;
        font-family: MicrosoftYaHei;
        font-size: .32rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #000000;
      }
      .title.er{
        margin-top:.8rem;
      }
      .btnSelect1{
        flex-wrap: wrap;
        div{
          margin-left:.35rem;
        }
        div:nth-of-type(4n){
          margin-top:.4rem;
          margin-left:0;
        }
      }
    }
  }
  .content{
    width:100%;
    height:auto;
    // padding-bottom:.4rem; 
    //  padding-top: .88rem;
    .item_content{
      width:100%;
      height:1.44rem;
      border-bottom:1px solid #e5e5e5;
      padding:.38rem .4rem;
      .bottom{
        margin-top:.06rem;
        width:100%;
        display:flex;
        justify-content: space-between;
        font-size:.24rem;
        color: #999999;
        font-family: MicrosoftYaHei;
        span:nth-of-type(1){

        }
      }
      .top{
        width:100%;
        display:flex;
        justify-content: space-between;
        span:nth-of-type(1){
          font-size:.32rem;
          line-height:.32rem;
          color:#000;
          font-family: MicrosoftYaHei;
        }
        .addPrice{
          font-family: MicrosoftYaHei;
          font-size:.32rem;
          line-height:.32rem;
          color: #ff0000 !important;
        }
        .delPrica{
          font-family: MicrosoftYaHei;
          font-size:.32rem;
          line-height:.32rem;
          color: #24bb03 !important;
        }
      }
    }
  }
}
</style>



