import request from '@/utils/request'
import Qs from 'qs'
// 账单列表
export function getBillList(data) {
    return request({
        transformRequest: [function(data) {
            // 对 data 进行任意转换处理
            return Qs.stringify(data)
        }],
        headers: {
            'deviceCode': 'A95ZEF1-47B5-AC90BF3'
        },
        url: '/api/app/merchant/wallet/bill/list',
        method: 'get',
        params: data
    })
}


